import Vue from 'vue';

import Router from 'vue-router';
Vue.use(Router)

//  axios
import axios from 'axios'
import login from '@/components/login.vue';		// 
import layout from '@/components/layout.vue';	// 
import index from '@/components/index.vue';		// 


// 
import team_list from '@/components/team/team_list.vue';		// 
import team_report from '@/components/team/team_report.vue';		// 
import leader_list from '@/components/team/leader_list.vue';		// 
import group_list from '@/components/team/group_list.vue';			// 
import crew_list from '@/components/team/crew_list.vue';			// 
// import team_online from '@/components/team/team_online.vue';		// 

import mining_recode from '@/components/mining/mining_recode.vue';		// 
import account_info from '@/components/account/account_info.vue';		// 

// 
import qrcode_manage from '@/components/qrcode/qrcode_manage.vue';	// 

// import web_notice from '@/components/notice/web_notice.vue';		// 

// 
import update_login_pass from '@/components/safe/update_login_pass.vue';	// 


// router
const router = new Router({
	routes: [
		{ path: '/', redirect: login },
		{ path: '/login', component: login },
		{
			path: '/admin', component: layout, children: [
				{
					path: 'index', component: index
				},
				{
					path: 'team_list', component: team_list
				},
				{
					path: 'team_report', component: team_report
				},
				{
					path: 'leader_list', component: leader_list
				},
				{
					path: 'group_list', component: group_list
				},
				{
					path: 'crew_list', component: crew_list
				},
				// {
				// 	path: 'team_online', component: team_online
				// },
				{
					path: 'update_login_pass', component: update_login_pass
				},
				{
					path:'mining_recode', component: mining_recode
				},
				{
					path: 'account_info', component: account_info
				},
				// {
				// 	path: 'web_notice', component: web_notice
				// },
				{
					path: 'qrcode_manage', component: qrcode_manage	
				},
			]
		},
		{
			// 
			path: '*',
			redirect: login
		}
	]
})



//  axios
axios.defaults.baseURL = window.location_url;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

// 
axios.interceptors.request.use(
	config => {
		if (!config.data) {
			config.data = {};
		}
		if (config.data.prompt === true) {
			config.data.__closeIndex = layer.msg('...', { icon: 16, shade: 0.01, time: 0 });
		};

		if ($.cookie('code_token')) {
			config.data.token = $.cookie('code_token');
		};
		// config.data.moduleId = $('.sideMenu a.router-link-active').parent().data('id')
		return config
	}, function (error) {
		return Promise.reject(error)
	}
)
// 
axios.interceptors.response.use(function (response) {

	if (response.config.data) {
		layer.close(JSON.parse(response.config.data).__closeIndex);
	}

	if (response.data.status == 'OK') {

		if (response.data.successMsg) {
			layer.msg(response.data.successMsg, {
				icon: '1'
				, time: 1500
			})
		}

		return response.data;
	} else {
		// 
		layer.msg(response.data.errorMsg, {
			icon: '2'
			, time: 1800
		})

		if (response.data.loginOut) {
			$.cookie('code_token', '', { expires: -1, path: '/' });
			setTimeout(() => {
				router.replace('/login')
			}, 2000)
		}

		console.error('~')

		return { status: 'error' };
	}
}, function (error) {
	layer.closeAll()

	// 
	layer.msg('', {
		icon: '2'
		, time: 1500
	})

	return { status: 'error' };
})
Vue.prototype.$axios = axios


// 
router.beforeEach((to, from, next) => {
	if (to.path === '/' || to.path === '/login') {
		window.$__addroutes = null;
		$.cookie('code_token', '', { expires: -1, path: '/' }); 	//  cookie
		$.cookie('userId', '', { expires: -1, path: '/' }); 	//  cookie
		$.cookie('userName', '', { expires: -1, path: '/' }); //  cookie
		layer.closeAll();
		next();
	}
	if ($.cookie('code_token') && $.cookie('code_token').length > 0) {
		next();
	} else {
		router.replace('/login');
		layer.closeAll();
	}
})

export default router;


