export default class Data {
	constructor(Vue) {
		this.Vue = Vue;

		// 
		this.tg = {
			has_pay: {
				0: '',
				1: '',
			},
			has_pay_color: {
				0: 'red',
				1: 'green'
			},
			has_upload: {
				0: '',
				1: '',
			},
			has_upload_color: {
				0: 'red',
				1: 'green'
			},
			pay_status: {
				0: '',
				1: '',
				2: ''
			},
			pay_status_color: {
				0: '',
				1: 'red',
				2: 'gray'
			},
			account_status: {
				0: '',
				1: '',
				2: '',
				3: '',
				4: '',
			},
			account_status_color: {
				0: 'green',
				1: 'red',
				2: 'gray',
				3: 'gray',
				4: 'gray',
			},
		}


		// 
		this.pageSizes = [100, 200, 500, 1000];


		// 
		this.reg = {
			ip: /^(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/,
		}
	}
}



